import CustomerRow from "./CustomerRow";
import styles from "./CustomerTable.module.css";

function CustomerTable(props) {
  const page = (function () {
    const newPage = props.displayList.slice(
      props.displayList.length - props.lowerIndex < props.customersOnPage
        ? props.lowerIndex
        : props.lowerIndex,
      props.lowerIndex + props.customersOnPage
    );
    if (newPage.length === 0) {
      props.onLastPage();
    }
    return newPage;
  })();

  function onDeleteHandler(customer) {
    props.onDelete(customer);
  }
  let tableIndex = props.lowerIndex;
  const list = page.map((customer) => {
    return (
      <tr className={styles["table-row"]} key={customer.id}>
        <td>{++tableIndex}</td>
        <CustomerRow
          customer={customer}
          onDelete={onDeleteHandler}
          onUpdate={props.onUpdate}
        ></CustomerRow>
      </tr>
    );
  });

  return (
    <table
      className={`${styles["customer-table"]} ${props.onSearch && styles.hide}`}
    >
      <thead>
        <tr>
          <th
            className={`${styles["table-header"]} ${styles["table-header--number"]}`}
          >
            â„–
          </th>
          <th className={styles["table-header"]}>First name</th>
          <th className={styles["table-header"]}>Last name</th>
          <th className={styles["table-header"]}>Email</th>
          <th className={styles["table-header"]}>Phone</th>
          <th
            className={`${styles["table-header"]} ${styles["table-header--action"]}`}
          >
            Action
          </th>
        </tr>
      </thead>
      <tbody>{list}</tbody>
    </table>
  );
}

export default CustomerTable;
