import Button from "./Button";
import styles from "./PageButtons.module.css";
function PageButtons(props) {
  function nextPageHandler(event) {
    event.preventDefault();
    props.onNextPage();
  }

  function previousPageHandler(event) {
    if (event !== undefined) {
      event.preventDefault();
    }
    props.onPreviousPage();
  }

  function lastPageHandler(event) {
    event.preventDefault();
    props.onLastPage();
  }

  function firstPageHandler(event) {
    event.preventDefault();

    props.onFirstPage();
  }

  return (
    <div className={styles["buttons-container"]}>
      <Button buttonText="&lt;&lt;" clickHandler={firstPageHandler}></Button>
      <Button buttonText="Previous" clickHandler={previousPageHandler}></Button>
      <p className={styles["page-number"]}>{props.currentPageNumber}</p>
      <Button
        buttonText="Next"
        clickHandler={nextPageHandler}
        additionalClass={props.disableNextButtons && "disabled"}
      ></Button>
      <Button
        buttonText="&gt;&gt;"
        clickHandler={lastPageHandler}
        additionalClass={props.disableNextButtons && "disabled"}
      ></Button>
    </div>
  );
}

export default PageButtons;
